'use client';

import React from "react";
import type { BackgroundTemplate } from "@/lib/backgrounds";
import type { SelectedColors } from "@/components/ColorPicker24";

export default function InquiryForm({
  text,
  colors,
  templates,
  tubeDiameter,
}: {
  text: string;
  colors: SelectedColors;
  templates: BackgroundTemplate[];
  tubeDiameter: 5 | 7 | 9;
}) {
  const [name, setName] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [note, setNote] = React.useState("");
  const [copied, setCopied] = React.useState(false);

  const summary = buildInquirySummary({
    text,
    colors,
    templates,
    tubeDiameter,
    name,
    email,
    note,
  });

  async function copyToClipboard() {
    try {
      await navigator.clipboard.writeText(summary);
      setCopied(true);
      window.setTimeout(() => setCopied(false), 1200);
    } catch {
      alert("コピーに失敗しました。手動で選択してコピーしてください。");
    }
  }

  return (
    <div className="rounded-2xl border border-white/10 bg-white/5 p-6">
      <h2 className="text-lg font-semibold">お問い合わせ（送信は後で差し替え）</h2>
      <p className="mt-1 text-sm text-zinc-300">
        まずは問い合わせ獲得（A）がゴールなので、現段階は「問い合わせ内容をコピー」できる形にしています。
      </p>

      <div className="mt-4 grid gap-4 sm:grid-cols-2">
        <div>
          <label className="text-sm font-semibold">お名前（任意）</label>
          <input
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder="例：山田 太郎"
            className="mt-2 w-full rounded-xl border border-white/10 bg-black/30 px-3 py-2 text-sm text-white placeholder:text-zinc-500 focus:outline-none focus:ring-2 focus:ring-fuchsia-300/60"
          />
        </div>
        <div>
          <label className="text-sm font-semibold">メール（任意）</label>
          <input
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            placeholder="example@gmail.com"
            className="mt-2 w-full rounded-xl border border-white/10 bg-black/30 px-3 py-2 text-sm text-white placeholder:text-zinc-500 focus:outline-none focus:ring-2 focus:ring-fuchsia-300/60"
          />
        </div>
      </div>

      <div className="mt-4">
        <label className="text-sm font-semibold">備考（任意）</label>
        <textarea
          value={note}
          onChange={(e) => setNote(e.target.value)}
          rows={3}
          placeholder="用途、希望サイズ、納期感など"
          className="mt-2 w-full rounded-xl border border-white/10 bg-black/30 px-3 py-3 text-sm text-white placeholder:text-zinc-500 focus:outline-none focus:ring-2 focus:ring-fuchsia-300/60"
        />
      </div>

      <div className="mt-4">
        <div className="flex items-center justify-between gap-3">
          <p className="text-sm font-semibold">問い合わせテンプレ</p>
          <button
            type="button"
            onClick={copyToClipboard}
            className="rounded-full bg-white px-4 py-2 text-sm font-semibold text-black hover:bg-zinc-200 transition-colors"
          >
            {copied ? "コピーしました" : "内容をコピー"}
          </button>
        </div>

        <pre className="mt-2 max-h-56 overflow-auto rounded-xl border border-white/10 bg-black/40 p-3 text-xs text-zinc-200 whitespace-pre-wrap">
{summary}
        </pre>
      </div>
    </div>
  );
}

function buildInquirySummary({
  text,
  colors,
  templates,
  tubeDiameter,
  name,
  email,
  note,
}: {
  text: string;
  colors: SelectedColors;
  templates: BackgroundTemplate[];
  tubeDiameter: 5 | 7 | 9;
  name: string;
  email: string;
  note: string;
}) {
  const lines: string[] = [];
  lines.push("【neon-sign-maker 問い合わせ】");
  if (name.trim()) lines.push(`お名前: ${name.trim()}`);
  if (email.trim()) lines.push(`メール: ${email.trim()}`);
  lines.push("");
  lines.push("■ 依頼内容（MVP）");
  lines.push("- 屋内ネオンサイン（個人向け）");
  lines.push(`- 発光幅（チューブ）: φ${tubeDiameter}mm 相当（デフォルト）`);
  lines.push("- 構造: ネオンチューブ + 透明アクリルパネル挟み込み（表裏） + スタンドオフ金具");
  lines.push("- 入力: 手書きアップロード + テキスト（任意）");
  lines.push("- 表現: 単線 / 14色パレット / 最大3色（主色1＋アクセント2）");
  lines.push("");
  lines.push("■ テキスト");
  lines.push(text.trim() ? text.trim() : "（未入力）");
  lines.push("");
  lines.push("■ 色");
  lines.push(`- 主色: ${colors.primary.name} (${colors.primary.hex})`);
  lines.push(`- アクセント1: ${colors.accent1.name} (${colors.accent1.hex})`);
  lines.push(`- アクセント2: ${colors.accent2.name} (${colors.accent2.hex})`);
  lines.push("");
  lines.push("■ 背景テンプレ（プレビュー）");
  for (const t of templates) {
    lines.push(`- ${t.name}`);
  }
  if (note.trim()) {
    lines.push("");
    lines.push("■ 備考");
    lines.push(note.trim());
  }
  return lines.join("\n");
}


