'use client';

import React from "react";
import UploadAndText from "@/components/UploadAndText";
import ColorPicker24, { type SelectedColors } from "@/components/ColorPicker24";
import NeonPreviewGrid from "@/components/NeonPreviewGrid";
import InquiryForm from "@/components/InquiryForm";
import { NEON_PALETTE_14 } from "@/lib/palette";
import { getDefaultBackground } from "@/lib/backgrounds";
import { NEON_PROTOCOL_V1 } from "@/lib/neonProtocol";

export default function StudioClient() {
  const defaultPrimary = NEON_PALETTE_14[0];
  const defaultAccent1 = NEON_PALETTE_14[5];
  const defaultAccent2 = NEON_PALETTE_14[9];

  const [sketchDataUrl, setSketchDataUrl] = React.useState<string | null>(null);
  const [text, setText] = React.useState("");
  const [colors, setColors] = React.useState<SelectedColors>({
    primary: defaultPrimary,
    accent1: defaultAccent1,
    accent2: defaultAccent2,
  });
  const [showPreview, setShowPreview] = React.useState(false);
  const tubeDiameter = NEON_PROTOCOL_V1.defaultTubeDiameter;
  const [isGenerating, setIsGenerating] = React.useState(false);
  const [aiImageDataUrl, setAiImageDataUrl] = React.useState<string | null>(null);
  const [aiError, setAiError] = React.useState<string | null>(null);
  const [apiKey, setApiKey] = React.useState<string>("");

  const canPreview = Boolean(sketchDataUrl || text.trim());
  const background = getDefaultBackground();

  // APIキー（ローカル保存）
  React.useEffect(() => {
    const stored = localStorage.getItem("neon-sign-maker-api-key");
    if (stored) setApiKey(stored);
  }, []);

  React.useEffect(() => {
    if (!apiKey) return;
    localStorage.setItem("neon-sign-maker-api-key", apiKey);
  }, [apiKey]);

  return (
    <main className="min-h-screen bg-black text-white">
      <div className="mx-auto max-w-5xl px-6 py-16 space-y-6">
        <div className="flex items-center justify-between gap-4">
          <a href="/" className="text-sm text-zinc-300 hover:text-white">
            ← 戻る
          </a>
          <div className="text-xs text-zinc-400">
            MVP: 問い合わせ獲得（A） / 出力: 設置イメージ（B）
          </div>
        </div>

        <header>
          <h1 className="text-3xl font-bold">Studio</h1>
          <p className="mt-2 text-zinc-300">
            画像アップロード（手書き）とテキストを元に、3背景で「飾った完成形」をプレビューします。
            {`仕上がりは「φ${tubeDiameter}mm相当のネオンチューブ + 透明アクリルパネル挟み込み（表裏） + 四隅金具」を想定。`}
          </p>
        </header>

        <UploadAndText
          imageDataUrl={sketchDataUrl}
          onImageChange={(next) => {
            setSketchDataUrl(next);
            setShowPreview(false);
            setAiImageDataUrl(null);
            setAiError(null);
          }}
          text={text}
          onTextChange={(next) => {
            setText(next);
            setShowPreview(false);
            setAiImageDataUrl(null);
            setAiError(null);
          }}
        />

        <ColorPicker24
          palette={NEON_PALETTE_14}
          value={colors}
          onChange={(next) => {
            setColors(next);
            setShowPreview(false);
            setAiImageDataUrl(null);
            setAiError(null);
          }}
        />

        <div className="rounded-2xl border border-white/10 bg-white/5 p-6">
          <div className="flex items-center justify-between gap-3">
            <div>
              <h2 className="text-lg font-semibold">プレビュー</h2>
              <p className="mt-1 text-sm text-zinc-300">
                まずはモックで確認 → 後からAI生成に差し替えます。
              </p>
            </div>
            <button
              type="button"
              disabled={!canPreview}
              onClick={() => setShowPreview(true)}
              className={[
                "rounded-full px-5 py-2 text-sm font-semibold transition-colors",
                canPreview ? "bg-white text-black hover:bg-zinc-200" : "bg-white/10 text-zinc-500 cursor-not-allowed",
              ].join(" ")}
            >
              プレビューを表示
            </button>
          </div>

          {!canPreview && (
            <p className="mt-3 text-xs text-zinc-400">
              ※ 手書きをアップロードするか、テキストを入力してください。
            </p>
          )}
        </div>

        {showPreview && (
          <>
            <NeonPreviewGrid
              templates={[background]}
              text={text}
              sketchDataUrl={sketchDataUrl}
              colors={colors}
              aiImageDataUrl={aiImageDataUrl}
            />

            <div className="rounded-2xl border border-white/10 bg-white/5 p-6">
              <div className="flex items-center justify-between gap-3">
                <div>
                  <h2 className="text-lg font-semibold">AIでリアルに生成</h2>
                  <p className="mt-1 text-sm text-zinc-300">
                    商品写真のような設置イメージを1枚生成します（ネオンチューブ＋透明アクリル＋四隅金具）。
                  </p>
                </div>
                <button
                  type="button"
                  disabled={!canPreview || isGenerating || !apiKey.trim()}
                  onClick={async () => {
                    if (!canPreview) return;
                    setIsGenerating(true);
                    setAiError(null);
                    try {
                      const resp = await fetch("/api/neon/generate", {
                        method: "POST",
                        headers: { "Content-Type": "application/json" },
                        body: JSON.stringify({
                          apiKey: apiKey.trim(),
                          sketchDataUrl,
                          text,
                          colors,
                          tubeDiameter,
                        }),
                      });
                      const data = await resp.json();
                      if (!resp.ok) throw new Error(data?.error || "生成に失敗しました");
                      setAiImageDataUrl(data.imageDataUrl);
                    } catch (e) {
                      setAiError(e instanceof Error ? e.message : "生成に失敗しました");
                    } finally {
                      setIsGenerating(false);
                    }
                  }}
                  className={[
                    "rounded-full px-5 py-2 text-sm font-semibold transition-colors",
                    !canPreview || isGenerating || !apiKey.trim()
                      ? "bg-white/10 text-zinc-500 cursor-not-allowed"
                      : "bg-fuchsia-300 text-black hover:bg-fuchsia-200",
                  ].join(" ")}
                >
                  {isGenerating ? "生成中..." : "リアル設置イメージを生成"}
                </button>
              </div>

              <div className="mt-4">
                <label className="text-sm font-semibold">APIキー（ローカル保存）</label>
                <input
                  value={apiKey}
                  onChange={(e) => setApiKey(e.target.value)}
                  placeholder="AIza...（このブラウザに保存されます）"
                  className="mt-2 w-full rounded-xl border border-white/10 bg-black/30 px-3 py-2 text-sm text-white placeholder:text-zinc-500 focus:outline-none focus:ring-2 focus:ring-fuchsia-300/60"
                />
                <p className="mt-2 text-xs text-zinc-400">
                  ※本番運用では環境変数（`NANO_BANANA_API_KEY`）に設定するのがおすすめです。
                </p>
              </div>

              {aiError && <p className="mt-3 text-sm text-red-300">{aiError}</p>}
            </div>

            <InquiryForm text={text} colors={colors} templates={[background]} tubeDiameter={tubeDiameter} />
          </>
        )}
      </div>
    </main>
  );
}


