'use client';

import React from "react";
import type { BackgroundTemplate } from "@/lib/backgrounds";
import type { SelectedColors } from "@/components/ColorPicker24";

export default function NeonPreviewGrid({
  templates,
  text,
  sketchDataUrl,
  colors,
  aiImageDataUrl,
}: {
  templates: BackgroundTemplate[];
  text: string;
  sketchDataUrl: string | null;
  colors: SelectedColors;
  aiImageDataUrl?: string | null;
}) {
  return (
    <div className="rounded-2xl border border-white/10 bg-white/5 p-6">
      <div className="flex items-end justify-between gap-4">
        <div>
          <h2 className="text-lg font-semibold">設置イメージ（背景3種）</h2>
          <p className="mt-1 text-sm text-zinc-300">
            同じデザインを3背景で比較。現段階はモック表示で、後からAI生成に差し替えます。
          </p>
        </div>
      </div>

      <div className="mt-4 grid gap-4 sm:grid-cols-1">
        {templates.map((t) => (
          <div key={t.id} className="rounded-2xl border border-white/10 overflow-hidden">
            <div className={["relative h-[420px]", t.className].join(" ")}>
              {aiImageDataUrl ? (
                <>
                  {/* eslint-disable-next-line @next/next/no-img-element */}
                  <img
                    src={aiImageDataUrl}
                    alt="AI生成の設置イメージ"
                    className="absolute inset-0 h-full w-full object-cover"
                  />
                  <div className="absolute inset-0 bg-black/10" />
                </>
              ) : (
                <div className="absolute inset-0 bg-black/30" />
              )}

              {!aiImageDataUrl && (
                <div className="absolute inset-0 grid place-items-center px-4">
                <div className="relative w-full max-w-[260px]">
                  {/* Acrylic panel mock (透明アクリルパネル + 四隅金具) */}
                  <div className="absolute left-1/2 top-1/2 h-[220px] w-[220px] -translate-x-1/2 -translate-y-1/2 rounded-2xl">
                    <div className="absolute inset-0 rounded-2xl bg-white/5 backdrop-blur-[1.5px]" />
                    <div className="absolute inset-0 rounded-2xl border border-white/20" />
                    <div className="absolute -inset-[6px] rounded-[22px] bg-black/10 blur-[10px]" />
                    {/* standoffs */}
                    {[
                      { x: "8px", y: "8px" },
                      { x: "calc(100% - 16px)", y: "8px" },
                      { x: "8px", y: "calc(100% - 16px)" },
                      { x: "calc(100% - 16px)", y: "calc(100% - 16px)" },
                    ].map((p, idx) => (
                      <div
                        key={idx}
                        className="absolute h-3.5 w-3.5 rounded-full bg-zinc-900/70 ring-1 ring-white/20 shadow"
                        style={{ left: p.x, top: p.y }}
                      >
                        <div className="absolute left-1/2 top-1/2 h-1.5 w-1.5 -translate-x-1/2 -translate-y-1/2 rounded-full bg-white/30" />
                      </div>
                    ))}
                  </div>

                  {/* Sketch overlay (optional) */}
                  {sketchDataUrl && (
                    // eslint-disable-next-line @next/next/no-img-element
                    <img
                      src={sketchDataUrl}
                      alt="スケッチ"
                      className="pointer-events-none absolute left-1/2 top-1/2 w-[92%] -translate-x-1/2 -translate-y-1/2 opacity-45 mix-blend-screen"
                      style={{
                        filter:
                          "invert(1) brightness(1.4) contrast(1.1) drop-shadow(0 0 10px " +
                          colors.primary.hex +
                          ") drop-shadow(0 0 18px " +
                          colors.accent1.hex +
                          ")",
                      }}
                    />
                  )}

                  {/* Neon text (optional) */}
                  {text.trim() && (
                    <div className="relative text-center">
                      <span
                        className="inline-block select-none whitespace-pre-wrap text-2xl font-extrabold tracking-wide"
                        style={{
                          backgroundImage: `linear-gradient(90deg, ${colors.primary.hex}, ${colors.accent1.hex}, ${colors.accent2.hex})`,
                          WebkitBackgroundClip: "text",
                          backgroundClip: "text",
                          color: "transparent",
                          textShadow: [
                            `0 0 6px ${colors.primary.hex}`,
                            `0 0 12px ${colors.primary.hex}AA`,
                            `0 0 18px ${colors.accent1.hex}88`,
                            `0 0 26px ${colors.accent2.hex}66`,
                          ].join(", "),
                        }}
                      >
                        {text}
                      </span>
                    </div>
                  )}
                </div>
              </div>
              )}

              {/* vignette */}
              <div className="absolute inset-0 pointer-events-none bg-[radial-gradient(circle_at_50%_40%,transparent_0%,rgba(0,0,0,0.55)_70%,rgba(0,0,0,0.75)_100%)]" />
            </div>
            <div className="p-3">
              <p className="text-sm font-semibold text-white">{t.name}</p>
              <p className="mt-1 text-xs text-zinc-300">{t.description}</p>
              <p className="mt-1 text-[11px] text-zinc-400">
                {aiImageDataUrl ? "AI生成のリアル設置イメージ" : "透明アクリル＋四隅金具の仕上がり（モック）"}
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}


