'use client';

import type { NeonColor } from "@/lib/palette";

type Slot = "primary" | "accent1" | "accent2";

export type SelectedColors = Record<Slot, NeonColor>;

export default function ColorPicker24({
  palette,
  value,
  onChange,
}: {
  palette: NeonColor[];
  value: SelectedColors;
  onChange: (next: SelectedColors) => void;
}) {
  const [activeSlot, setActiveSlot] = React.useState<Slot>("primary");

  function setSlotColor(slot: Slot, color: NeonColor) {
    onChange({ ...value, [slot]: color });
  }

  return (
    <div className="rounded-2xl border border-white/10 bg-white/5 p-6">
      <div className="flex items-end justify-between gap-4">
        <div>
          <h2 className="text-lg font-semibold">色（14色パレット / 最大3色）</h2>
          <p className="mt-1 text-sm text-zinc-300">
            主色1＋アクセント2（配分はAIに任せる想定）
          </p>
        </div>
        <div className="text-xs text-zinc-400">選択中: {slotLabel(activeSlot)}</div>
      </div>

      <div className="mt-4 grid grid-cols-3 gap-3">
        <SlotButton
          label="主色"
          color={value.primary}
          active={activeSlot === "primary"}
          onClick={() => setActiveSlot("primary")}
        />
        <SlotButton
          label="アクセント1"
          color={value.accent1}
          active={activeSlot === "accent1"}
          onClick={() => setActiveSlot("accent1")}
        />
        <SlotButton
          label="アクセント2"
          color={value.accent2}
          active={activeSlot === "accent2"}
          onClick={() => setActiveSlot("accent2")}
        />
      </div>

      <div className="mt-5 grid grid-cols-7 gap-2">
        {palette.map((c) => (
          <button
            key={c.id}
            type="button"
            onClick={() => setSlotColor(activeSlot, c)}
            className="group relative h-9 w-9 rounded-lg border border-white/10 hover:border-white/25 transition-colors"
            title={`${c.name} (${c.hex})`}
          >
            <span
              className="absolute inset-0 rounded-lg"
              style={{
                backgroundColor: c.hex,
                boxShadow: `0 0 0 1px rgba(0,0,0,0.25) inset, 0 0 18px ${c.hex}55`,
              }}
            />
            <span className="sr-only">{c.name}</span>
          </button>
        ))}
      </div>

      <p className="mt-4 text-xs text-zinc-400">
        ※現段階のプレビューはモックです（AI生成に差し替え予定）。
      </p>
    </div>
  );
}

function SlotButton({
  label,
  color,
  active,
  onClick,
}: {
  label: string;
  color: NeonColor;
  active: boolean;
  onClick: () => void;
}) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={[
        "rounded-xl border px-3 py-3 text-left transition-colors",
        active ? "border-white/30 bg-white/10" : "border-white/10 bg-white/5 hover:border-white/20",
      ].join(" ")}
    >
      <div className="flex items-center justify-between gap-3">
        <div>
          <p className="text-xs text-zinc-400">{label}</p>
          <p className="mt-0.5 text-sm font-semibold">{color.name}</p>
        </div>
        <span
          className="h-7 w-7 rounded-lg border border-white/10"
          style={{ backgroundColor: color.hex, boxShadow: `0 0 14px ${color.hex}66` }}
        />
      </div>
    </button>
  );
}

function slotLabel(slot: Slot) {
  if (slot === "primary") return "主色";
  if (slot === "accent1") return "アクセント1";
  return "アクセント2";
}

// React import is intentionally placed at bottom to keep the component compact in diffs.
import React from "react";


